import React, { Component } from 'react'
import PropTypes from 'prop-types'

export default class Players extends Component {
    static propTypes = {
        prop: PropTypes
    }

    clickCall(e) {
        console.log(this)
    }

    render() {

        const { name, cost, category } = this.props

        return (
            <div>
                <>

                    <div className="card" style={{ backgroundColor: "lightGray" }} onClick={this.clickCall.bind(name)}>



                        <h3 className="card-title" style={{ backgroundColor: "gray", color: "wheat", cursor: "pointer" }}> ✨{name}</h3>



                        <div className="card-text">
                            <ul style={{ listStyle: "square" }}>

                                <li style={{ fontSize: "15px" }}>Fiyat: <b>{cost}</b> </li>
                                <li style={{ fontSize: "15px" }}>Kategori: <b>{category}</b></li>
                            </ul>
                        </div>
                    </div>

                </>
            </div>
        )
    }
}
